.outer {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding-top: var(--top-pad);
}
.container {
  display: flex;
  flex-direction: row;
  gap: 1rem;
  width: 100%;
  box-sizing: border-box;
}

.rotation {
  writing-mode: vertical-lr;
  transform: rotate(180deg);
  text-align: left;
}

.digital-garden {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  flex: 1;
  width: calc(100vw - 2rem);
  background-image: linear-gradient(#b0b0b0, #f6f6f6);
  padding: 1rem;
  box-shadow: 5px 10px 20px rgba(0, 0, 0, 0.5);
  /* border-radius: 15px; */
  margin-bottom: 50px;
}

.gardenMenu {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}
.gardenContent {
  height: calc(100vh - 245px);
  overflow-y: scroll;
  /* overflow-x: hidden; */
}
.gardenContent::-webkit-scrollbar {
  display: none;
}
.drop-down {
  height: fit-content;
  background-color: #f6f6f6;
  border-radius: 24px;
  border: 1px solid rgba(0, 0, 0, 1);
  align-self: stretch;
  padding: 0.25rem;
  font-size: 1rem;
  line-height: 1.8rem;
  font-family: "GT America";
  font-weight: 200;
  color: black;
  text-transform: uppercase;
}
.drop-down:hover {
  background-color: #82f74f;
  cursor: pointer;
}
.plus {
  width: 10px;
}
.ddMenu {
  display: flex;
  width: 100%;
}
.ddMenu:first-child {
  width: calc(100% - 14px);
}
@media only screen and (max-width: 970px) {
  .outer {
    padding-top: var(--mob-pad-top);
  }
  .container {
    padding-top: 0;
    width: 100%;
    /* padding-left: var(--mob-pad-left); */
  }
  .digital-garden {
    flex-direction: column;
    width: calc(100% - 4rem);
  }
  .gardenOuter {
    width: calc(100vw - 1rem);
  }
  .gardenContent {
    width: calc(100vw - 4.5rem);
  }
}
